import { state, actions, mutations} from "~/store/index.js"
/**
 * @description: 通过code获取label
 * @param {*}
 * @return {*}
 * @author: syx
 */
const GetLabelByCode = (val, type, defaultVal = "") => { 
  return new Promise((resolve, reject) => {
    const {format} = state.dictSetting
    const dictList = state.dictCodes
    let list = []
    //如果没找到此类型的数据
    if (!dictList || !dictList[type]) {
      if (state.isGettingDictTypes.includes(type) || state.isGettingAllDictTypes) { //如果有别的地方在请求这个字典类型数据  轮询查看是否请求结束
        const timer = setInterval(() => {
          if (!(state.isGettingDictTypes.includes(type) || state.isGettingAllDictTypes)) {
            clearInterval(timer)
            list = state.dictCodes[type]
            if (!list) {
              reject(`不存在类型为：${type} 的字典字段，请确认后再填写`)
              return
            }
            const targetItem = list.find(item => { return item[format.value] === val })
            resolve(targetItem ? targetItem[format.label] : defaultVal) 
          }
        }, 1000)
      } else {
        actions.getDictDataObj(type).then(data => {
          list = data[type]
          if (!list) {
            reject(`不存在类型为：${type} 的字典字段，请确认后再填写`)
            return
          }
          const targetItem = list.find(item => { return item[format.value] === val })
          resolve(targetItem ? targetItem[format.label] : defaultVal) 
        })
      }
    } else {
      list = dictList[type]
      const targetItem = list.find(item => { return item[format.value] === val })
      resolve(targetItem ? targetItem[format.label] : defaultVal) 
    }
  })
}

export const getLabelByCode = GetLabelByCode

/**
 * @description: 通过codes获取labels
 * @param {*}
 * @return {*}
 * @author: syx
 */
export function GetLabelByCodesDothings(values, list, format, defaultVal) {
  const targetArr = []
  for (let i = 0; i < values.length; i++) {
    const findItem = list.find(item => { return item[format.value] === values[i] })
    if (findItem) {
      targetArr.push(findItem)
    } else {
      targetArr.push({
        [format.label]: defaultVal,
        [format.value]: values[i]
      })
    }
  }
  return targetArr
}
