/**
 * @description: 通过dictType返回该字典类型数据
 * @param {*}
 * @return {*}
 * @author: syx
 */
const GetDictObjByDictTypes = (dictTypes) => {
  return new Promise((resolve, reject) => {
    let values = []
    let typeStr = ""
    if (Array.isArray(dictTypes)) {
      typeStr = dictTypes.join(",")
      values = dictTypes
    } else if (typeof dictTypes === "string") {
      values = dictTypes.split(",")
    }

    const dictList = state.dictCodes
    const setting = state.dictSetting
    //不存在字典值
    if (!dictList) {
      //添加正在请求的字典数据
      mutations.ADD_IS_EGTTING_DICT_TYPES(typeStr)
      setting.getDictCodeApi(setting.formatterRequest(setting.query, typeStr)).then(data => {
        //去除正在请求的字典数据
        mutations.REMOVE_IS_EGTTING_DICT_TYPES(typeStr)
        const dictCodeList = setting.formatterDictList(data, dictTypes)
        const copyObj = JSON.parse(JSON.stringify(dictCodeList))
        const allDictObj = Object.assign(state.dictCodes, copyObj)
        setting.storage.setItem(setting.dictDataKey, JSON.stringify(allDictObj))
        mutations.SET_DICT_CODES(allDictObj)
        resolve(dictCodeList)
      })
    } else {
      const needGetArr = [] //保留需要请求的字典数组类型
      const haveDictObj = {} //已经有的字典数据类型数据
      for (let i = 0; i < values.length; i++) {
        if (dictList[values[i]]) {
          haveDictObj[values[i]] = dictList[values[i]]
          continue
        }
        needGetArr.push(values[i])
      }

      if (needGetArr.length > 0) {
        //添加正在请求的字典数据
        mutations.ADD_IS_EGTTING_DICT_TYPES(needGetArr.join(","))
        setting.getDictCodeApi(setting.formatterRequest(setting.query, needGetArr.join(","))).then(data => {
          //去除正在请求的字典数据
          mutations.REMOVE_IS_EGTTING_DICT_TYPES(needGetArr.join(","))
          const dictCodeList = setting.formatterDictList(data, dictTypes)
          const copyObj = JSON.parse(JSON.stringify(dictCodeList))
          const allDictObj = Object.assign(state.dictCodes, copyObj)
          setting.storage.setItem(setting.dictDataKey, JSON.stringify(allDictObj))
          mutations.SET_DICT_CODES(allDictObj)

          const resolveDict = Object.assign(haveDictObj, dictCodeList)

          const noDictTypeArr = []
          for (const key in resolveDict) {
            if (!resolveDict[key] || resolveDict[key].length === 0) {
              noDictTypeArr.push(key)
            }
          }
          if (noDictTypeArr.length > 0) {
            reject(`不存在类型为：${noDictTypeArr.join("、")} 的字典字段，请确认后再填写`)
          }
          resolve(resolveDict)
        })
      } else {
        resolve(haveDictObj)
      }
    }
  })
}
export const getDictObjByDictTypes = GetDictObjByDictTypes