<template>
  <div class="van-cascaders-dict">
    <van-field v-on="$listeners" v-bind="$attrs" readonly clickable :value="showValue" is-link @click="handleClick" />
    <van-popup v-model="showPicker" round position="bottom">
      <van-cascaders ref="cascader" class="cascader" :placeholder="cascaderPlaceholder" @click-tab="handleClickTab" @close="handleClose" @change="handdleChange" @finish="handleFinish" v-model="selectValue" :closeable="false" :field-names="setting" v-bind="$attrs" :options="areaTree">
        <template slot="title">
          <div class="van-picker__cancel" @click="handleCancel">{{cancelButtonText}}</div>
          <div class="title">{{$attrs.title}}</div>
          <div class="van-picker__confirm" @click="handleConfirm(selectValue)">{{confirmButtonText}}</div>
        </template>
      </van-cascaders>
    </van-popup>
  </div>
</template>
<script>
import { state, actions } from "~/store/index.js"
export default {
  name: "van-cascader-dict",
  data() {
    return {
      showValue: "",
      showPicker: false,
      list: [], //数据列表
      svalue: this.value,
      type: "", //请求字典类型
      setting: {
        text: state.dictSetting.treeSetting.labelField,
        value: state.dictSetting.treeSetting.idField,
        children: state.dictSetting.treeSetting.childrenField
      },
      selectValue: null,
      selectedOptions: [],
      endTreeSetting: {},
      tabIndex: null //选中的数据的层级
    }
  },
  props: {
    tips: {
      type: String
    },
    //可以选中的最小层级
    minSelectLevel: {
      type: [Number, String]
    },
    //最大层级
    maxLevel: {
      type: [Number, String]
    },
    cascaderPlaceholder: {
      type: String,
      default: "请选择"
    },
    data: {
      type: Array,
      default: () => {
        return []
      }
    },
    // 树形数据
    treeData: {
      type: Array,
      default: () => {
        return []
      }
    },
    treeSetting: {
      type: Object,
      default: () => {
        return {}
      }
    },
    defaultValue: {
      type: String,
      default: ""
    },
    spacer: {
      type: String,
      default: "/"
    },
    //选到最后一级时触发确认
    finish: {
      type: Boolean,
      default: false
    },
    cancelButtonText: {
      type: String,
      default: "取消"
    },
    confirmButtonText: {
      type: String,
      default: "确认"
    },
    keyValue: {
      type: Boolean,
      default: false
    },
    dictType: {
      type: [String, Object]
    }, //请求的码表值
    value: {
    }, //接受外部v-model传入的值
    disableObj: {
      type: Object
    },
    //过滤函数
    filterDataFun: {
      type: Function
    },
    //禁用函数
    disabledDataFun: {
      type: Function
    },
    //van-cascader配置
    props: {
      type: Object,
      default: () => {
        return {
        }
      }
    }
  },
  computed: {
    dictCodes() {
      return state.dictCodes
    },
    dictSetting() {
      return state.dictSetting
    },
    isGettingAllDictTypes() {
      return state.isGettingAllDictTypes
    },
    isGettingDictTypes() {
      return state.isGettingDictTypes
    },
    areaTree() {
      if (this.treeData.length > 0) {
        return this.treeData
      }
      if (this.data.length === 0 && this.list.length === 0) {
        return []
      }

      const allAllTreeData = this.data.length > 0 ? this.$listToTree(this.data, this.endTreeSetting) : this.$listToTree(this.list, this.endTreeSetting)
      const treeArr = JSON.parse(JSON.stringify(allAllTreeData))
      if (this.maxLevel) {
        this.filterLevel(treeArr, +this.maxLevel)
      }
      return treeArr || []
    }
  },
  watch: {
    isGettingDictTypes(val) {
      if (!val.includes(this.type) && this.data.length === 0 && this.treeData.length === 0) {
        this.getDictionary_option()
      }
    },
    //请求全部字典后 重新渲染页面
    isGettingAllDictTypes(val) {
      if (!val && this.data.length === 0 && this.treeData.length === 0) {
        this.getDictionary_option() 
      }
    },
    //判断下拉框的值是否有改变
    value(val) {
      this.svalue = val//②监听外部对props属性result的变更，并同步到组件内的data属性myResult中
    },
    svalue(val, oldVal) {
      if (val !== oldVal) {
        this.$emit("input", val)//③组件内对myResult变更后向外部发送事件通知
      }
    },
    showPicker(val) {
      if (!val) {
        this.selectValue = null
        this.handleClose()
      } else {
        this.selectValue = this.value
      }
    },
    props: {
      handler: function(val) {
        Object.assign(this.setting, val)
        this.endTreeSetting.idField = this.setting.value
        this.endTreeSetting.labelField = this.setting.text
        this.endTreeSetting.childrenField = this.setting.children
      },
      immediate: true
    },
    //监听数据变化时，赋值
    areaTree(val) {
      if (!this.value || val.length === 0) { return }
      const path = this.$getTreePathByValue(val, this.value, this.endTreeSetting)
      this.showValue = this.setShowValue(path, this.spacer)
    }
  },
  created() {
    //深拷贝一下  避免影响到全局配置
    const setTreeSetting = JSON.parse(JSON.stringify(state.dictSetting.treeSetting))
    this.endTreeSetting = Object.assign(setTreeSetting, this.treeSetting)

    Object.assign(this.setting, this.props)
    this.endTreeSetting.idField = this.setting.value
    this.endTreeSetting.labelField = this.setting.text
    this.endTreeSetting.childrenField = this.setting.children
  },
  mounted() {
    // this.svalue=this.value;//初始话下拉框的值
    if (this.data.length === 0 && this.treeData.length === 0) {
      this.getDictionary_option()
    }
 
    if (this.treeData.length > 0 && this.value) {
      const path = this.$getTreePathByValue(this.treeData, this.value, this.endTreeSetting)
      this.showValue = this.setShowValue(path, this.spacer)
    }
    
  },
  methods: {
    // 赋值
    setShowValue(targetArr, spacer) {
      let str = ""
      for (let i = 0; i < targetArr.length; i++) {
        str += targetArr[i][this.endTreeSetting.labelField]
        if (i !== targetArr.length - 1) {
          str += spacer
        }
      }
      return str
    },
    //过滤层级
    filterLevel(arr, maxLevel) {
      for (let i = 0; i < arr.length; i++) {
        const item = arr[i]
        const currentLevel = item[this.endTreeSetting.levelField]
        if (currentLevel === maxLevel) {
          delete item.children
          continue
        } else {
          this.filterLevel(item[this.endTreeSetting.childrenField], maxLevel)
        }
      }
    },
    //全部选项选择完成后触发
    handleFinish(data) {
      if (this.finish) {
        this.handleConfirm(data.value)
      }
      this.$emit("finish", data)
    },
    //改变事件
    handdleChange(data) {
      this.tabIndex = data.tabIndex
      this.selectedOptions = data.selectedOptions 
      this.$emit("change", data)
    },
    //关闭事件
    handleClose() {
      this.$emit("close")
    },
    handleClickTab(data) {
      this.$emit("click-tab", data)
    },
    //点击事件
    handleClick() {
      if (this.$attrs.disabled === true || this.$attrs.disabled === "") {
        return
      }
      this.showPicker = true
    },
    //点击确定
    handleConfirm(value) {
      console.log(this.selectedOptions)
      if (this.selectedOptions[this.tabIndex].disabled) {
        return 
      }
      if (this.minSelectLevel&& +this.minSelectLevel > (this.tabIndex + 1)) {
        const msg = this.tips || `请选择至少第${this.minSelectLevel}级`
        this.$toast(msg)
        return
      }
      this.svalue = value
      this.showPicker = false
      if (!this.keyValue) {
        this.$emit("dictChange", this.svalue)
      } else {
        this.$emit("dictChange", this.selectedOptions)
      }

      this.showValue = this.setShowValue(this.selectedOptions, this.spacer)
    },
    //点击取消
    handleCancel() {
      this.showPicker = false
    },
    // 获取下拉框数据
    async getDictionary_option() {
      if (!this.dictType) {
        return
      }
      //获取该类型字典数据
      let dictData = []
      if (typeof this.dictType === "object") {
        this.type = this.dictType.type
        await actions.getDictDataObj(this.type)
        dictData = [].concat(this.dictCodes[this.type])
        const { filters, filterType = this.endTreeSetting.idField, reverse = false } = this.dictType
        const filtersArr = typeof (filters) === "string" ? filters.split(",") : filters
        if (reverse) {
          dictData = dictData.filter(item => {
            return !filtersArr.includes(item[filterType])
          })
        } else {
          dictData = dictData.filter(item => {
            return filtersArr.includes(item[filterType])
          })
        }
      } else {
        this.type = this.dictType
        await actions.getDictDataObj(this.type)
        dictData = [].concat(this.dictCodes[this.type])
      }

      //使用函数过滤数据
      if (this.filterDataFun) {
        dictData = this.filterDataFun(dictData)
      } else {
        dictData = this.dictSetting.filterDataFun(dictData)
      }
      //使用函数禁用数据
      if (this.disabledDataFun) {
        dictData = this.disabledDataFun(dictData)
      } else {
        dictData = this.dictSetting.disabledDataFun(dictData)
      }

      if (this.disableObj) {
        const { disableValue, disableType = this.endTreeSetting.idField, reverse = false } = this.disableObj
        const disableList = typeof (disableValue) === "string" ? disableValue.split(",") : disableValue
        if (reverse) {
          dictData = dictData.map(item => {
            const targetItem = JSON.parse(JSON.stringify(item))
            targetItem.disabled = !disableList.includes(targetItem[disableType]) || targetItem.disabled
            return targetItem
          })
        } else {
          dictData = dictData.map(item => {
            const targetItem = JSON.parse(JSON.stringify(item))
            targetItem.disabled = disableList.includes(targetItem[disableType]) || targetItem.disabled
            return targetItem
          })
        }
      }

      this.list = dictData

    }
  }
}
</script>
<style scoped lang="less">
.van-cascaders-dict {
  ::v-deep .van-popup {
    .van-cascaders {
      .van-cascaders__header {
         padding: 0;
        .van-cascaders__title {
          width: 100%;
          display: flex;
          justify-content: flex-start;
          align-content: center;
          .van-picker__cancel {
            color: #969799;
          }
          .title{
            flex-grow: 1;
            text-align: center;
            font-weight: 500;
            font-size: 16px;
            line-height: 20px;
          }
          .van-picker__confirm{
            color: #576b95;
          }
        }
      }
    }
  }
}
</style>