import { state, actions} from "~/store/index.js"

/**
 * @description: 树形字典通过code获取label
 * @param {*}
 * @return {*}
 * @author: syx
 */
import {getTreeItemByCode} from "~/functions/listToTree"

const GetTreeLabelByCode = (val, type, defaultVal = "", spacer = "/", setting) => { 
  return new Promise((resolve, reject) => {
    const {treeSetting} = state.dictSetting
    const resultSetting = Object.assign(treeSetting, setting)
    const dictList = state.dictCodes
   
    const formatFun = function(arr){
      return arr.map(item => {
        return item[resultSetting.labelField]
      }).join(spacer)
    }
     
    let list = []
    //如果没找到此类型的数据
    if (!dictList || !dictList[type]) {
      if (state.isGettingDictTypes.includes(type) || state.isGettingAllDictTypes) { //如果有别的地方在请求这个字典类型数据  轮询查看是否请求结束
        const timer = setInterval(() => {
          if (!(state.isGettingDictTypes.includes(type) || state.isGettingAllDictTypes)) {
            clearInterval(timer)
            list = state.dictCodes[type]
            if (!list) {
              reject(`不存在类型为：${type} 的字典字段，请确认后再填写`)
              return
            }
            const targetItem = list.find(item => { return item[resultSetting.idField] === val })
            resolve(targetItem ? formatFun(getTreeItemByCode(targetItem[resultSetting.idField], list, resultSetting)) : defaultVal) 
          }
        }, 1000)
      } else {
        actions.getDictDataObj(type).then(data => {
          list = data[type]
          if (!list) {
            reject(`不存在类型为：${type} 的字典字段，请确认后再填写`)
            return
          }
          const targetItem = list.find(item => { return item[resultSetting.idField] === val })
          resolve(targetItem ? formatFun(getTreeItemByCode(targetItem[resultSetting.idField], list, resultSetting)) : defaultVal) 
        })
      }
    } else {
      list = dictList[type]
      const targetItem = list.find(item => { return item[resultSetting.idField] === val })
      resolve(targetItem ? formatFun(getTreeItemByCode(targetItem[resultSetting.idField], list, resultSetting)) : defaultVal)
    }
  })
}
export const getTreeLabelByCode = GetTreeLabelByCode
 
/**
  * @description: 树形数据通过codes 获取labels
  * @param {*}
  * @return {*}
  * @author: syx
  */
export function getTreeLabelByCodesDothings(values, list, defaultVal, formatFunIn, resultSetting) {
  const targetArr = []
  for (let i = 0; i < values.length; i++) {
    const findItem = list.find(item => { return item[resultSetting.idField] === values[i] })
    if (findItem) {
      targetArr.push(formatFunIn(getTreeItemByCode(findItem[resultSetting.idField], list, resultSetting)))
    } else {
      targetArr.push(defaultVal)
    }
  }
  return targetArr
}
const GetTreeLabelByCodes = (vals, type, defaultVal = "", formatFunIn, setting, formatFunOut, spacer = ",") => {
  return new Promise((resolve, reject) => {
    let values = []
    if (Array.isArray(vals)) {
      values = vals
    } else if (typeof vals === "string") {
      values = vals.split(spacer)
    }
 
    const {treeSetting} = state.dictSetting
    const resultSetting = Object.assign(treeSetting, setting)
    const dictList = state.dictCodes
 
    if (!formatFunIn) {
      formatFunIn = function(arr){
        return arr.map(item => {
          return item[resultSetting.labelField]
        }).join("/")
      }
    }
 
    if (!formatFunOut) {
      formatFunOut = function(arr) {
        return arr.join(",")
      }
    }
     
    let list = []
    //如果没找到此类型的数据
    if (!dictList || !dictList[type]) {
      if (state.isGettingDictTypes.includes(type) || state.isGettingAllDictTypes) { //如果有别的地方在请求这个字典类型数据  轮询查看是否请求结束
        const timer = setInterval(() => {
          if (!(state.isGettingDictTypes.includes(type) || state.isGettingAllDictTypes)) {
            clearInterval(timer)
            list = state.dictCodes[type]
            if (!list) {
              reject(`不存在类型为：${type} 的字典字段，请确认后再填写`)
              return
            }
            const targetArr = getTreeLabelByCodesDothings(values, list, defaultVal, formatFunIn, resultSetting)
            resolve(targetArr&&targetArr.length > 0 ? formatFunOut(targetArr) : defaultVal)
          }
        }, 1000)
      } else {
        actions.getDictDataObj(type).then(data => {
          list = data[type]
          if (!list) {
            reject(`不存在类型为：${type} 的字典字段，请确认后再填写`)
            return
          }
          const targetArr = getTreeLabelByCodesDothings(values, list, defaultVal, formatFunIn, resultSetting)
          resolve(targetArr&&targetArr.length > 0 ? formatFunOut(targetArr) : defaultVal)
        })
      }
    } else {
      list = dictList[type]
      const targetArr = getTreeLabelByCodesDothings(values, list, defaultVal, formatFunIn, resultSetting)
      resolve(targetArr&&targetArr.length > 0 ? formatFunOut(targetArr) : defaultVal)
    }
  })
}
 
export const getTreeLabelByCodes = GetTreeLabelByCodes
 
