import dayjs from "dayjs"

const DEFAULT_FORMATS = {
  date: "YYYY-MM-DD",
  month: "YYYY-MM",
  datetime: "YYYY-MM-DD HH:mm:ss",
  time: "HH:mm:ss",
  year: "YYYY"
}
//通过code获取中文
const DayFormat = (day, format, defaultValue = "") => { 
  format = DEFAULT_FORMATS[format] || format
  const formatDay = Number(day) ? Number(day) : day
  return dayjs(formatDay).format(format) === "Invalid Date" ? defaultValue : dayjs(formatDay).format(format)
}
DayFormat.install = function(Vue){
  Vue.filter("dayFormat", DayFormat)
}
export const dayFormat = DayFormat

