import { state, actions } from "~/store/index.js"

/**
 * @description: 通过code获取label
 * @param {*}
 * @return {*}
 * @author: syx
 */
const GetLabelByCode = (val, type, defaultVal = "") => { 
  const {format} = state.dictSetting
  const dictList = state.dictCodes
  let list = []
  //如果没找到此类型的数据
  if (!dictList || !dictList[type]) {
    actions.getDictDataObj(type)
    return defaultVal
  } else {
    list = dictList[type]
    const targetItem = list.find(item => { return item[format.value] === val })
    return targetItem ? targetItem[format.label] : defaultVal
  }
}
GetLabelByCode.install = function(Vue){
  Vue.filter("getLabelByCode", GetLabelByCode)
}
export const getLabelByCode = GetLabelByCode

/**
 * @description: 通过codes获取labels
 * @param {*}
 * @return {*}
 * @author: syx
 */
import {GetLabelByCodesDothings} from "~/functions/dict"

const GetLabelByCodes = (vals, type, defaultVal = "", formatFun, spacer = ",") => {

  let values = []
  if (Array.isArray(vals)) {
    values = vals
  } else if (typeof vals === "string") {
    values = vals.split(spacer)
  }

  const {format} = state.dictSetting
  const dictList = state.dictCodes

  if (!formatFun) {
    formatFun = function(arr){
      return arr.map(item => {
        return item[format.label]
      }).join(",")
    }
  }
  let list = []
  //如果没找到此类型的数据
  if (!dictList || !dictList[type]) {
    actions.getDictDataObj(type)
    return defaultVal
  } else {
    list = dictList[type]
    const targetArr = GetLabelByCodesDothings(values, list, format, defaultVal)
    return targetArr&&targetArr.length > 0 ? formatFun(targetArr) : defaultVal
  }
}
GetLabelByCodes.install = function(Vue){
  Vue.filter("getLabelByCodes", GetLabelByCodes)
}
export const getLabelByCodes = GetLabelByCodes

