<template>
  <div class="van-datetime-dict">
    <van-field v-on="$listeners" type="text" v-bind="$attrs" readonly clickable :value="showValue" is-link  @click="handleClick" />
    <van-popup v-model="showPicker" round position="bottom">
      <van-datetime-picker ref="datetimePicker" v-model="currentDate" v-bind="$attrs" @change="handleChange" @cancel="handleCancel" @confirm="onConfirm" >
        <template v-slot:confirm>
          <div class="slot-btn"><span v-if="showForver" class="select" @click.stop="forever">永久</span><span>{{$attrs.confirmButtonText || "确认"}}</span></div>
        </template>
      </van-datetime-picker>
    </van-popup>
  </div>
</template>
<script>
export default {
  name: "van-datetime-dict",
  data() {
    return {
      showValue: "",
      showPicker: false,
      svalue: this.value,
      currentDate: null,
      indexs: null
    }
  },
  props: {
    showValueLabel: {
      type: String,
      default: "永久"
    },
    forverValue: {
      default: "forver"
    },
    //是否展示永久按钮
    showForver: {
      type: Boolean,
      default: false
    },
    //日期过滤格式
    dateFormat: {
      type: String
    },
    //默认值
    defaultValue: {
      type: String,
      default: ""
    },
    value: {
    } //接受外部v-model传入的值
  },
  computed: {

  },
  watch: {
    //判断下拉框的值是否有改变
    value(val) {
      this.svalue = val//②监听外部对props属性result的变更，并同步到组件内的data属性myResult中
    },
    svalue(val, oldVal) {
      if (val !== oldVal) {
        this.$emit("input", val)//③组件内对myResult变更后向外部发送事件通知
      }
    },
    showPicker(val) {
      if (this.indexs) {
        this.$nextTick(() => {
          this.$refs.datetimePicker.getPicker().setIndexes(this.indexs)
        })
        return
      }
      if (val) {
        this.$nextTick(() => {
          setTimeout(() => {
            this.indexs = this.$refs.datetimePicker.getPicker().getIndexes()
          }, 100)
        })
      }
    }
  },
  created() {
    if (this.showForver && this.value === this.forverValue) {
      this.showValue = this.showValueLabel
    } else {
      if (this.value) {
        this.formatValue(this.value)
      }
      if (this.$attrs.type === "time") {
        this.currentDate = this.value
      } else if (this.$attrs.type === "month-day") {
        const date = (new Date().getFullYear() + "-" + this.value).replace(/-|\./g, "/")
        this.currentDate = new Date(date)
      } else if (this.$attrs.type === "datehour") {
        const date = (this.value + ":00:00").replace(/-|\./g, "/")
        this.currentDate = new Date(date)
      } else {
        const date = this.value.replace(/-|\./g, "/")
        this.currentDate = new Date(date)
      }
    }
  },
  methods: {
    handleChange(value) {
      this.$emit("change", value)
    },
    //取消
    handleCancel() {
      this.showPicker = false
      this.$emit("cancel")
    },
    //点击事件
    handleClick() {
      if (this.$attrs.disabled === true || this.$attrs.disabled === ""){
        return 
      }
      this.showPicker = true
    },
    formatValue(value) {
      const dateObj = {
        "date": "date",
        "year-month": "month",
        "month-day": "MM-DD",
        "datehour": "YYYY-MM-DD HH"
      }
      if (this.$attrs.type === "time") {
        this.svalue = value
      } else {
        const dateFormat = this.dateFormat || dateObj[this.$attrs.type] || "datetime"
        this.svalue = this.$dayFormat(value, dateFormat, this.defaultValue)
      }
      this.showValue = this.svalue
    },
    //点击确定
    onConfirm(value) {
      this.formatValue(value)
      this.indexs = this.$refs.datetimePicker.getPicker().getIndexes()
      this.showPicker = false
      this.$emit("dictChange", value)
      this.$emit("confirm", value)
    },
    forever() {
      this.svalue = this.forverValue
      this.showValue = this.showValueLabel
      this.showPicker = false
      this.$emit("forver", this.forverValue)
    }
  }
}
</script>
<style lang="less" scoped>
.van-datetime-dict{
  ::v-deep .slot-btn{
    .select{
      margin-right: 14px;
    }
  }
}

</style>