<template>
  <div class="van-select-dict">
    <van-field
      v-on="$listeners"
      v-bind="$attrs"
      readonly
      clickable
      :value="showValue"
      is-link
      @click="handleClick"
    />
    <van-popup v-model="showPicker" round position="bottom">
      <van-picker
        ref="selectPicker"
        v-on="$listeners"
        v-bind="$attrs"
        show-toolbar
        :value-key="dictSetting.format.label"
        :columns="list"
        @cancel="showPicker = false"
        @confirm="onConfirm"
      />
    </van-popup>
  </div>
</template>
<script>
import { state, actions } from "~/store/index.js"
export default {
  name: "van-select-dict",
  data() {
    return {
      defaultIndex: "0",
      showPicker: false,
      list: [], //数据列表
      svalue: this.value,
      type: "" //请求字典类型
    }
  },
  props: {
    data: {
      type: Array,
      default: () => {
        return []
      }
    },
    //格式化数据
    format: {
      type: Object,
      default: () => {
        return {
          label: "label",
          value: "value"
        }
      }
    },
    //是否返回键值
    keyValue: {
      type: Boolean,
      default: false
    },
    dictType: {
      type: [String, Object]
    }, //请求的码表值
    value: {}, //接受外部v-model传入的值
    disableObj: {
      type: Object
    },
    //过滤函数
    filterDataFun: {
      type: Function
    },
    //禁用函数
    disabledDataFun: {
      type: Function
    }
  },
  computed: {
    dictCodes() {
      return state.dictCodes
    },
    dictSetting() {
      return state.dictSetting
    },
    isGettingAllDictTypes() {
      return state.isGettingAllDictTypes
    },
    isGettingDictTypes() {
      return state.isGettingDictTypes
    },
    selectItem() {
      return this.list.find(item => {
        return item[this.dictSetting.format.value] === this.svalue
      })
    },
    //展示的值
    showValue() {
      return this.selectItem
        ? this.selectItem[this.dictSetting.format.label]
        : ""
    }
  },
  watch: {
    isGettingDictTypes(val) {
      if (!val.includes(this.type) && this.data && this.data.length === 0) {
        this.getDictionary_option()
      }
    },
    //请求全部字典后 重新渲染页面
    isGettingAllDictTypes(val) {
      if (!val && this.data && this.data.length === 0) {
        this.getDictionary_option()
      }
    },
    //判断下拉框的值是否有改变
    value(val) {
      this.svalue = val //②监听外部对props属性result的变更，并同步到组件内的data属性myResult中
    },
    svalue(val, oldVal) {
      if (val !== oldVal) {
        this.$emit("input", val) //③组件内对myResult变更后向外部发送事件通知
      }
    },
    showPicker(val) {
      const index = this.list
        .map(item => item[this.dictSetting.format.value])
        .indexOf(this.svalue)
      this.defaultIndex = index === -1 ? 0 : index
      this.$nextTick(() => {
        this.$refs.selectPicker.setColumnIndex(0, this.defaultIndex)
      })
    },
    data: {
      handler: function(val) {
        this.list = JSON.parse(JSON.stringify(val))
        if (this.list.length > 0 && this.list[0][this.format.value]) {
          this.list = this.list.map(item => {
            return {
              [this.dictSetting.format.value]: item[this.format.value],
              [this.dictSetting.format.label]: item[this.format.label]
            }
          })
        } else {
          this.list = this.list.map(item => {
            return {
              [this.dictSetting.format.value]: item,
              [this.dictSetting.format.label]: item
            }
          })
        }
        
        if (!(this.$attrs.required === true || this.$attrs.required === "")) {
          this.list.unshift({
            [this.dictSetting.format.value]: "null",
            [this.dictSetting.format.label]: "请选择"
          })
        }
      },
      immediate: true      
    }
  },
  mounted() {
    // this.svalue=this.value;//初始话下拉框的值
    if (this.data&&this.data.length === 0) {
      this.getDictionary_option()
    }
  },
  methods: {
    //点击事件
    handleClick() {
      if (this.$attrs.disabled === true || this.$attrs.disabled === "") {
        return
      }
      this.showPicker = true
    },
    //点击确定
    onConfirm(value) {
      this.svalue =
        value[this.dictSetting.format.value] === "null"
          ? ""
          : value[this.dictSetting.format.value]
      this.showPicker = false
      if (!this.keyValue) {
        this.$emit("dictChange", this.svalue)
      } else {
        const dictObj =
          value[this.dictSetting.format.value] === "null"
            ? {
              [this.dictSetting.format.value]: "",
              [this.dictSetting.format.label]: ""
            }
            : value
        this.$emit("dictChange", dictObj)
      }
    },
    // 获取下拉框数据
    async getDictionary_option() {
      if (!this.dictType || (this.data && this.data.length > 0)) {
        return
      }
      //获取该类型字典数据
      let dictData = []
      if (typeof this.dictType === "object") {
        this.type = this.dictType.type
        await actions.getDictDataObj(this.type)
        dictData = [].concat(this.dictCodes[this.type])
        const {
          filters,
          filterType = this.dictSetting.format.value,
          reverse = false
        } = this.dictType
        const filtersArr =
          typeof filters === "string" ? filters.split(",") : filters
        if (reverse) {
          dictData = dictData.filter(item => {
            return !filtersArr.includes(item[filterType])
          })
        } else {
          dictData = dictData.filter(item => {
            return filtersArr.includes(item[filterType])
          })
        }
      } else {
        this.type = this.dictType
        await actions.getDictDataObj(this.type)
        dictData = [].concat(this.dictCodes[this.type])
      }

      //使用函数过滤数据
      if (this.filterDataFun) {
        dictData = this.filterDataFun(dictData)
      } else {
        dictData = this.dictSetting.filterDataFun(dictData)
      }
      //使用函数禁用数据
      if (this.disabledDataFun) {
        dictData = this.disabledDataFun(dictData)
      } else {
        dictData = this.dictSetting.disabledDataFun(dictData)
      }

      if (this.disableObj) {
        const {
          disableValue,
          disableType = this.dictSetting.format.value,
          reverse = false
        } = this.disableObj
        const disableList =
          typeof disableValue === "string"
            ? disableValue.split(",")
            : disableValue
        if (reverse) {
          dictData = dictData.map(item => {
            const targetItem = JSON.parse(JSON.stringify(item))
            targetItem.disabled =
              !disableList.includes(targetItem[disableType]) ||
              targetItem.disabled
            return targetItem
          })
        } else {
          dictData = dictData.map(item => {
            const targetItem = JSON.parse(JSON.stringify(item))
            targetItem.disabled =
              disableList.includes(targetItem[disableType]) ||
              targetItem.disabled
            return targetItem
          })
        }
      }

      this.list = dictData
      if (!(this.$attrs.required === true || this.$attrs.required === "")) {
        this.list.unshift({
          [this.dictSetting.format.value]: "null",
          [this.dictSetting.format.label]: "请选择"
        })
      }
    }
  }
}
</script>