<template>
  <div class="van-field-dict">
    <van-field v-on="$listeners" v-bind="$attrs" @focus="handleFocus" @blur="handleBlur"></van-field>
    <van-field v-if="desensitization" v-show="showCamouflage"  :name="$attrs.name&&$attrs.name + 'Camouflage'"  v-bind="$attrs"  class="camouflage" v-model="sValue"></van-field>
  </div>
</template>

<script>
export default {
  name: "van-field-dict",
  props: {
    desensitization: [String, Number], //脱敏类型或者 展示前面几个数
    defaultValue: {
      type: String, //缺省时展示
      default: ""
    },
    desensitizationEnd: Number, //展示后面几个
    desensitizationPlaceholder: Number //中间占位符个数
  },
  data() {
    return {
      sValue: "",
      showCamouflage: false
    }
  },
  created() {
    this.handleBlur()
  },
  methods: {
    //聚焦
    handleFocus() {
      if (this.$attrs.readonly === true || this.$attrs.readonly === ""){
        return 
      }
      this.showCamouflage = false
    },
    //失去焦点
    handleBlur() {
      if (this.desensitization) {
        this.sValue = this.$desensitization(this.$attrs.value, this.desensitization, this.defaultValue, this.desensitizationEnd, this.desensitizationPlaceholder)
        this.showCamouflage = true
      }
    }
  }
}
</script>

<style lang="less" scoped>
.van-field-dict{
  position: relative;
  .van-cell.van-field.camouflage{
    position: absolute;
    left: 0;
    top: 0;
    pointer-events: none;
  }
}
</style>