const ClickDebounce = {
  inserted: function(el, binding) {
    let timer
    el.addEventListener("click", () => {
      if (timer) {
        clearTimeout(timer)
      }
      timer = setTimeout(() => {
        binding.value()
      }, 1000)
    })
  },
  unbind(el) {
    el.removeEventListener("click", el.handler)
  }
}

ClickDebounce.install = function(Vue) {
  Vue.directive("click-debounce", ClickDebounce)
}
export const clickDebounce = ClickDebounce

const InputDebounce = {
  inserted: function(el, binding) {
    let timer
    el.addEventListener("keyup", () => {
      if (timer) {
        clearTimeout(timer)
      }
      timer = setTimeout(() => {
        binding.value(el.value)
      }, 1000)
    })
  },
  unbind(el) {
    el.removeEventListener("keyup", el.handler)
  }
}

InputDebounce.install = function(Vue) {
  Vue.directive("input-debounce", InputDebounce)
}
export const inputDebounce = InputDebounce